<script>
export default {
  data() {
    return {
      active: "a",
      value: "",
      size: "25px",
      imgUrl: "https://imgapi.jiaston.com/BookFiles/BookImages/",
      reche: "",
      thrust1: "",
      thrust2: "",
      ticket1: "",
      ticket2: "",
      reader1: "",
      reader2: "",
      ulsW: null,
      vshow: false,
      event: "",
      images: [
        "http://img04.sogoucdn.com/app/a/10190001/951453183751374",
        "http://img02.sogoucdn.com/app/a/10190001/721453183751383",
        "http://img01.sogoucdn.com/app/a/10190001/1C1453183751304",
        "http://img03.sogoucdn.com/app/a/10190001/211454045131456"
      ]
    };
  },
  mounted() {
    this.vshow = false;
  },
  methods: {
    //推荐
    commend() {
      let self = this;
      this.$axios({
        methods: "GET",
        dataType: "jsonp",
        url: "http://localhost:1000/getjson/xiaoshuo-commend"
      }).then(reche => {
        reche.data.data.BookList.forEach(v => {
          v.Img = this.imgUrl + v.Img;
        });
        this.reche = reche.data.data.BookList;
        localStorage.setItem(
          "book-commend",
          JSON.stringify(reche.data.data.BookList)
        );
        setTimeout(() => {
          this.$nextTick(() => {
            let lisW = this.$refs.lisW[0].clientWidth + 15;
            this.ulsW = lisW * reche.data.data.BookList.length;
          });
        }, 100);
      });
    },
    //强推
    push() {
      this.$axios({
        mathods: "GET",
        dataType: "jsonp",
        url: "http://localhost:1000/getjson/xiaoshuo-total"
      }).then(reche => {
        reche.data.data.BookList.forEach(v => {
          v.Img = this.imgUrl + v.Img;
        });
        localStorage.setItem(
          "book-push",
          JSON.stringify(reche.data.data.BookList)
        );
        this.thrust1 = reche.data.data.BookList.slice(0, 5);
        this.thrust2 = reche.data.data.BookList.slice(5, 10);
      });
    },
    //霸王票
    ticket() {
      this.$axios({
        mathods: "GET",
        dataType: "jsonp",
        url: "http://localhost:1000/getjson/xiaoshuo-over"
      }).then(reche => {
        reche.data.data.BookList.forEach(v => {
          v.Img = this.imgUrl + v.Img;
        });
        localStorage.setItem(
          "book-ticket",
          JSON.stringify(reche.data.data.BookList)
        );
        this.ticket1 = reche.data.data.BookList.slice(0, 5);
        this.ticket2 = reche.data.data.BookList.slice(5, 10);
      });
    },
    //读者栽培
    reader() {
      this.$axios({
        mathods: "GET",
        dataType: "jsonp",
        url: "http://localhost:1000/getjson/xiaoshuo-collect"
      }).then(reche => {
        reche.data.data.BookList.forEach(v => {
          v.Img = this.imgUrl + v.Img;
        });
        localStorage.setItem(
          "book-reader",
          JSON.stringify(reche.data.data.BookList)
        );
        this.reader1 = reche.data.data.BookList.slice(0, 5);
        this.reader2 = reche.data.data.BookList.slice(5, 10);
      });
    },
    //动态搜索框
    forumscoll(event) {
      this.event = event.target.scrollTop;
      if (parseInt(event.target.scrollTop) >= 300) {
        this.vshow = true;
      } else {
        this.vshow = false;
      }
    },
    getbook(o) {
      this.$router.push(o);
    },
    togo() {
      this.$router.push({ name: "rankinglist" });
    }
  },
  created() {
    this.commend();
    this.push();
    this.ticket();
    this.reader();
    this.vshow = false;
  }
};
</script>
